/* The switch - the box around the checkbox_checkbox_slider */
.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  margin: 0px;
}
/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
/* The checkbox_slider */
.checkbox_slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border: 1px solid #ddd;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.checkbox_slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 0px;
  bottom: 0px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
input:checked + .checkbox_slider {
  background-color: #00b300;
}
input:focus + .checkbox_slider {
  box-shadow: inset 0 0 1px #2196f3;
}
input:checked + .checkbox_slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}
/* Rounded checkbox_sliders */
.checkbox_slider.round {
  border-radius: 20px;
}
.checkbox_slider.round:before {
  border-radius: 50%;
}
